import { useEffect } from 'react';
import ReactGA from 'react-ga4';

const Analytics = () => {
  useEffect(() => {
    const id = setTimeout(() => {
      ReactGA.initialize(process.env.REACT_APP_GOOGLE_ANALYTICS_ID);
      ReactGA.send('pageview');
    }, 4000);
    return () => clearTimeout(id);
  }, []);

  return null;
};

export default Analytics;
